<template>
  <div id="askMentor">
    <h1> Mentor </h1>
    <p>Mentor </p>
    <p></p>
    <br>
    <el-form
      :model="question"
      ref="question"
      label-position="top"
      label-width="100px"
    >
      <el-row type="flex" justify="center" align="middle" :gutter="20">
        <el-col :span="8" :xs="20">
          <el-form-item label=" " prop="projectId">
            <el-input
              v-model.lazy="question.projectId"
              placeholder=""
              :maxlength="3"
            ></el-input>
          </el-form-item>
          <el-form-item label=" Mentor " prop="mentorId">
            <el-input
              v-model.lazy="question.mentorId"
              placeholder=" Mentor "
              :maxlength="3"
            ></el-input>
          </el-form-item>
          <el-form-item label=" " prop="title">
            <el-input
              v-model.lazy="question.title"
              placeholder="310"
              :maxlength="10"
            ></el-input>
          </el-form-item>
          <el-form-item label=" " prop="description">
            <el-input
              type="textarea"
              v-model.lazy="question.description"
              placeholder="100"
              :autosize="{ minRows: 2, maxRows: 10}"
              :maxlength="100"
            >
            </el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="askQuestion()"></el-button>
            <el-button @click="resetQuestion()"></el-button>
            <p></p>
          </el-form-item>
        </el-col>
      </el-row>
    </el-form>
  
  </div>
</template>

<script>
// import _ from 'lodash'

import { FirebaseApp } from '@/service/firebase.js'

export default {
  name: 'askMentor',
  mounted () {
    this.$emit('loaded')
  },
  data () {
    return {
      question: {
        title: '',
        mentorId: '',
        projectId: '',
        description: ''
      }
    }
  },
  firebase () {
    const that = this
    return {
      questions: {
        source: FirebaseApp.database().ref('/mentor/questions'),
        asObject: true,
        readyCallback: () => {
          console.log('[askMentor] Fetched `mentor.questions`!')
        },
        cancelCallback: function (e) {
          console.log('[askMentor] Fetched `mentor.questions` cancel!', e)
          that.$message.error('')
        }
      }
    }
  },
  computed: {
  },
  methods: {
    askQuestion () {
      this.$firebaseRefs.questions.push(this.question)
        .then(() => {
          this.resetQuestion()
          this.$message({message: '', type: 'success'})
        })
        .catch((e) => { this.$message.error('') })
    },
    resetQuestion () {
      this.question = {
        title: '',
        mentorId: '',
        projectId: '',
        description: ''
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="sass" scoped>
#askMentor


</style>



