<template>
  <div class='oauthlogin'>

    <!-- 1.  -->
    <template v-if="user !== null">
      <div v-if="user.photoURL"><img :src="user.photoURL" alt="avatar" style="max-width: 50px"></div>
      <el-button @click="signOut"></el-button>
    </template>

    <!-- 2. Email -->
    <template v-else>
      <div class="panel panel-default">
        <div class="panel-body">

          <div class="text-center">
            <!-- a.  -->
            <template v-if="enableGoogleLoginFlag">
              <el-button @click="signinWithGoogle" :size="isPhone ? 'small' : 'large'" class="btn-primary-google">
                <span class="icon-wrap"><img class="icon" src="../../assets/google-logo-bg.png"></span>
                <span class="label">Google</span></el-button>
            </template>

            <template v-if="enableFacebookLoginFlag">
              <el-button @click="signinWithFB" :size="isPhone ? 'small' : 'large'" class="btn-primary-facebook">
                <span class="icon-wrap"><img class="icon" src="../../assets/facebook-logo.jpg"></span>
                <span class="label">Facebook</span></el-button><br>
            </template>

            <!-- b. Email/ -->
            <template v-if="enableEmailPassLoginFlag">
              <br><br><hr style="margin:0"><span style="position:relative;top:-0.6rem;background-color:white;">  </span>

              <h3>Email</h3>
              <button @click="showEmailPassField = true" v-show="!showEmailPassField" class="btn btn-default"> / </button>
            </template>
          </div>

          <template v-if="enableEmailPassLoginFlag">
            <div v-show="showEmailPassField">
              <div class="form-group">
                <label for="email"></label>
                <input v-model.trim="email" type="email" class="form-control" id="email">
              </div>
              <div class="form-group">
                <label for="pwd"></label>
                <input v-model="pwd" type="password" class="form-control" id="pwd">
              </div>

              <!-- / -->
              <!-- <p @click="toggleShowResetBtn" class="text-right"><u></u></p> -->
              <p @click="showResetBtn = !showResetBtn" class="text-right"><u></u></p>

              <!--  -->
              <button @click="signInWithEmailAndPassword" type="button" class="btn btn-primary"></button>
              <button @click="signup" type="button" class="btn btn-default"></button>
            </div>

            <!--  -->
            <div v-show="showResetBtn" class="panel panel-default">
              <div class="panel-body">
                <div class="input-group">
                  <input v-model="email" type="text" class="form-control" placeholder=" email ">
                  <span class="input-group-btn">
                    <button @click="sendResetMail" class="btn btn-primary" type="button"></button>
                  </span>
                </div><!-- /input-group -->
              </div>
            </div>
          </template>

        </div><!-- panel-body -->
      </div><!-- panel panel-default -->
    </template><!-- 2. Email -->

  </div>
</template>

<script>
import firebase from 'firebase'
import { FirebaseApp } from '@/service/firebase.js'
const max480 = window.matchMedia('(max-device-width: 480px)').matches

export default {
  name: 'oauthlogin',
  data () {
    return {
      user: null,
      userRef: null,
      enableEmailPassLoginFlag: false, // REVIEW: toggle to enable email/pass login
      enableGoogleLoginFlag: false, // REVIEW: toggle to enable email/pass login
      enableFacebookLoginFlag: true, // REVIEW: toggle to enable email/pass login
      email: null,
      pwd: null,
      showEmailPassField: false,
      showResetBtn: false,
      isPhone: max480
    }
  },
  created () {
    FirebaseApp.auth().onAuthStateChanged((U) => {
      this.showResetBtn = false
      if (U) {
        this.user = {
          uid: U.uid,
          email: U.email || U.providerData[0].email,
          displayName: U.providerData[0].displayName || U.email.split('@')[0],
          photoURL: U.providerData[0].photoURL || 'http://i.imgur.com/1Us6LVp.png'
        }
        if (!this.user.uid || !this.user.email || !this.user.displayName || !this.user.photoURL) {
          console.warn('[OAuthLogin] ?', this.user)
        }
        this.setUserToDatabase(this.user.uid, this.user.email, this.user.displayName, this.user.photoURL)
        console.log('[OAuthLogin] : ', this.user.displayName)
      } else {
        this.user = null
        console.log('[OAuthLogin] ')
      }
    })
  },
  methods: {
    setUserToDatabase (uid, email, displayName, photoURL) {
      //  Firebase Databse
      FirebaseApp.database().ref('users/' + uid).set({
        uid: uid,
        displayName: displayName,
        email: email,
        photoURL: photoURL
      }).catch(err => console.error('[OAuthLogin] :', err.message))
    },

    // Google 
    signinWithGoogle () {
      const provider = new firebase.auth.GoogleAuthProvider()
      provider.addScope('profile')
      provider.addScope('email')
      const promise = FirebaseApp.auth().signInWithPopup(provider)
      promise.then((result) => {
        console.log('Google : ', result)
        this.$message({message: 'Google ', type: 'success'})
      }).catch((err) => {
        console.log('Google : ', err.code, err.message)
        this.$notify({
          title: 'Google ',
          message: `Chrome${err.message}`,
          type: 'warning',
          duration: 0
        })
      })
    },

    // Facebook 
    signinWithFB () {
      const provider = new firebase.auth.FacebookAuthProvider()
      const promise = FirebaseApp.auth().signInWithPopup(provider)
      promise.then((result) => {
        console.log('Facebook : ', result)
        this.$message({message: 'Facebook ', type: 'success'})
      }).catch((err) => {
        console.log('Facebook : ', err.code, err.message)
        this.$notify({
          title: 'Facebook ',
          message: `Google Chrome${err.message}`,
          type: 'warning',
          duration: 0
        })
      })
    },
    // 
    signOut () {
      FirebaseApp.auth().signOut()
      this.email = ''
      this.pwd = ''
    },

    // XXX deprecated
    signInWithEmailAndPassword () {
      const promise = FirebaseApp.auth().signInWithEmailAndPassword(this.email, this.pwd)
      promise.catch((err) => {
        // 
        console.log('[SignInOrSignUp.vue] ', err)
        switch (err.code) {
          case 'auth/wrong-password':this.$message({message: 'email', type: 'warning'}); break
          case 'auth/invalid-email':this.$message({message: 'emailemail', type: 'warning'}); break
          case 'auth/user-disabled':this.$message({message: ' lawrencechou1024ATgmail.com ', type: 'warning'}); break
          case 'auth/user-not-found':this.$message({message: '', type: 'warning'}); break
          default:
            this.$message.error({message: ':' + err.message})
        }
      })
    },

    // XXX deprecated
    signup () {
      const promise = FirebaseApp.auth().createUserWithEmailAndPassword(this.email, this.pwd)
      promise.catch((err) => {
        // 
        console.log('[SignInOrSignUp.vue] ', err)
        switch (err.code) {
          case 'auth/email-already-in-use': this.$message({message: 'Email', type: 'warning'}); break
          case 'auth/invalid-email': this.$message({message: 'emailemail', type: 'warning'}); break
          case 'auth/operation-not-allowed': this.$message({message: 'Email lawrencechou1024ATgmail.com ', type: 'warning'}); break
          case 'auth/weak-password': this.$message({message: '', type: 'warning'}); break
          default:
            this.$message.error({message: ':' + err.message})
        }
      })
    },

    // XXX deprecated
    sendResetMail () {
      const promise = FirebaseApp.auth().sendPasswordResetEmail(this.email)
      promise.then(() => {
        alert('' + this.email + '')
        console.log('[SignInOrSignUp.vue] ', this.email)
      }).catch((err) => {
        console.log('>> sendPasswordResetEmail()', err)
        switch (err.code) {
          case 'auth/invalid-email': alert('email'); break
          case 'auth/user-not-found': alert('email'); break
          default: alert(err)
        }
      })
    }
  }
}
</script>

<style lang='css'>

.icon-wrap {
    height: 1rem;
    position: relative;
    /*border: 1px solid Green;*/
}

.icon {
    /*border: 1px solid Red;*/
    position: absolute;
    height: 100%;
}

.label {
  padding-left: 1.5rem;
}

.btn-primary-facebook:hover {
    color: #fff;
    background-color: #223567;
    border-color: #204d74;
}
.btn-primary-facebook {
    color: #fff;
    background-color: #324e97;
    border-color: #324e97;
}

.btn-primary-google:hover {
    color: #fff;
    background-color: #3a72d0;
    border-color: #3a72d0;
}

.btn-primary-google {
    color: #fff;
    background-color: #4285F4;
    border-color: #4285F4;
}

</style>



